import React from 'react';
import "./index.scss";
import { getNameAbbr, getColorIndex } from '../../utils';
import "styles/bg-colors.scss";

export default function Header({userInfo, logout}) {
  return (
      <div className="home-header">
          { userInfo.avatar && (<img src={userInfo.avatar || ""} className="home-header-avatar" alt="user avatar" />)}
          { !userInfo.avatar && (
              <span className={`home-header-name-abbr bgColor${getColorIndex(userInfo.displayName)}`}>{getNameAbbr(userInfo.displayName)}</span>
            )
          }
          <span className="home-header-name">{userInfo.displayName}</span>
          <span className="home-header-logout" onClick={logout}>Logout</span>
      </div>
  )
}
