import React, {useRef, useState, useEffect, useCallback} from 'react';
import "./install-popup.scss"

export default function InstallPopup() {
  let installEvent = useRef(null);
  let [visible, setVisible] = useState(true);
  let installPromptCallback = useCallback( (e) => {
    e.preventDefault();
    if(window.sessionStorage.getItem('noPrompt')) return;
    installEvent.current = e;
    return () => {
      window.removeEventListener('beforeinstallprompt', installPromptCallback);
    }
  });
  
  let onClickInstall = useCallback(() => {
    if(!installEvent.current) return;
    installEvent.current.prompt();
    installEvent.current.userChoice.then( choice => {
      console.log("install pwa result: ", choice);
    }).catch( err => {
      console.log("install prompt error: ", err);
    })
    setVisible(false);
    window.sessionStorage.setItem('noPrompt', true);
  });
  
  let onClickLater = useCallback(() => {
    setVisible(false);
    window.sessionStorage.setItem('noPrompt', true);
  });
  
  useEffect(() => {
    window.addEventListener('beforeinstallprompt', installPromptCallback);
  }, []);
  
  return  visible && (
    <div className={`install-popup ${visible ? "install-popup__visible" : ""}`}>
        <div>You can add me to desktop now.</div>
        <div className="install-btns">
          <button onClick={onClickInstall} className="btn-install">Install</button>
          <button onClick={onClickLater}>Later</button>
        </div>
    </div>
  )
}